<template>
  <div class="main">
    <div class="crumb_box">
      <div class="search_box">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item>首页</el-breadcrumb-item>
          <el-breadcrumb-item>签名设置</el-breadcrumb-item>
        </el-breadcrumb></div>
    </div>
    <div class="clear"></div>
    <div class="application_item">
      <div class="autograph">
        <div class="autograph-area"><img src="../assets/images/none.png"></div>
        <div class="autograph-grybg">
          <p class="fr">请选择签名方式：
            <button type="button" class="el-button el-button--success el-button--small"><span>上传签名</span></button>
            <button type="button" class="el-button el-button--primary el-button--small"><span>手写签名</span></button>
          </p>
        </div>
      </div>
      <div class="clear"></div>
      <div  class="autograph-grytxt">
        <h3>小提示</h3>
        <p>1、你可以通过上传签名或手绘签名来设置您的个性化电子签名；</p>
        <p>2、设置好电子签名后，您在操作审批流程的时候可以使用电子签名；</p>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "SignatureSetup",
    data () {
      return {
        imageUrl:"",
        UserNameForm: {},
        contactsForm:{
          contacts:[
            // {
            //   // title: "",
            //   // phone: ""
            // }
          ]
        }
      }
    },
    created(){

    },
    computed: {

    },
    methods:{



    }
  }
</script>

<style scoped>
  .el-form{width: 100%;}
  .avatar-uploader .el-upload{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;position:relative;overflow:hidden}
  .avatar-uploader .el-upload:focus,.avatar-uploader .el-upload:hover{border-color:#409eff}
  .avatar-uploader .avatar-uploader-icon{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;font-size:28px;color:#8c939d;width:178px;height:178px;line-height:178px;text-align:center}
  .avatar-uploader .avatar{width:178px;height:178px;display:block}
  .el-input {position: relative;font-size: 14px;display: inline-block;}
  .el-form-item__error {position: relative;}
  .iconremove{font-size: 20px;color: rgb(0, 170, 238);margin-left: 5px;cursor: pointer;}
  .auth_text_blue{color: #fff;background: #409EFF;    border: 1px solid #409EFF;transition: .1s;font-weight: 500;padding: 8px 18px;font-size: 12px;border-radius: 4px; cursor: pointer}
</style>



